import {required} from "vuelidate/lib/validators";

export default {
  name: "reg-subject",
  data() {
    return {
      header_title: "" ,
      dismissCountDown: 10,
      delComfirm: false,
      formData: {
        id: 0,
        lessonId: 0,
        description: ''
      },
      subjet_options: [],
      t_table_fields: [
        {
          key: 'subject',
          label: 'Subject',
          sortable: true
        },
        {
          key: 'description',
          label: 'Description',
          sortable: true
        },
        {
          key: 'createdAt',
          label: 'Created At',
          sortable: true
        },
        {
          key: 'acceptStatus',
          label: 'Status',
          sortable: true
        },
        {
          key: 'edit',
          label: '',
          sortable: false
        },
        {
          key: 'remove',
          label: '',
          sortable: false
        },
      ],
      t_subject_items:[]

    }
  },
  validations: {
    formData:{
      lessonId:{
        required,
      },
      description:{
        required,
      }
    },

  },
  mounted() {

    this.fillSubject();
    this.reloadTable();
  },
  methods: {
    hideModal() {
      this.$refs['edit-modal'].hide()
    },

    prepareAddNew()
    {
      this.header_title = "Add New Lesson"
      this.formData.id = 0
      this.formData.description = ""
      this.formData.lessonId = 0
      this.$refs['edit-modal'].show()
    },

    prepareEdit(row)
    {
      this.header_title = "Edit Lesson"
      this.formData.id = row.item.id
      this.formData.description = row.item.description
      this.formData.lessonId = row.item.lessonId
      this.$refs['edit-modal'].show()
    },

    cancel(){
      this.formData.id = 0
      this.hideModal('edit-modal')
    },

    makeToast(variant = 'success', header = 'Message', msg = '') {
      this.$bvToast.toast(this.$t(msg), {
        autoHideDelay: 3000,
        title: header,
        variant: variant,
        solid: true,
        appendToast: true,
        headerClass: "d-none"
      })
    },


    fillSubject() {

      this.$axios.post('subject/subjectlist', null , this.config).then((res) => {
        // localStorage.setItem('userToken', res.data)
        // this.$router.push('/profile')
        this.subjet_options = res.data
        console.log(res.data)
      }).catch(error => {
        if (error.response.data.errors) {
          this.errors = error.response.data.errors
        }
        console.log(error)
      })
    },

    reloadTable() {
      this.$axios.post('/subject/items' , null, this.config ).then((res) =>
      {
        this.t_subject_items = [];
        this.t_subject_items = res.data
      }).catch(error => {
        if (error.response.data.errors) {
          this.errors = error.response.data.errors
        }
        console.log(error)
      });

    },

    showDeleteComfirm(rowId) {
      this.$bvModal.msgBoxConfirm(this.$t('delete_confirm'), {
        title: 'Please Confirm',
        size: 'sm',
        buttonSize: 'sm',
        okVariant: 'danger',
        okTitle: 'YES',
        cancelTitle: 'NO',
        headerClass: 'p-2 border-bottom-0',
        footerClass: 'p-2 border-top-0',
        hideHeaderClose: false,
        centered: true
      })
        .then(value => {
          this.delComfirm = value

          function deleteRow(id) {
            this.$axios.post('subject/delete' , null , this.config).then((res) =>
            {
              this.makeToast( 'success' , 'Message' ,  this.$t('success_delete') )
              //debug
              console.log(res.data);

              return true;

            }).catch(error => {
              if (error.response.data.errors) {
                this.errors = error.response.data.errors
              }
              console.log(error)
            });

          }

          if(value===true)
          {
            let retValue = deleteRow(rowId.id);
            if(retValue.result === true)
              reloadTable();
          }
        })
        .catch(err => {
          // An error occurred
        })
    },

    saveEdit()
    {
      this.$v.$reset()
      this.$v.formData.$touch()
      if (this.$v.formData.$invalid) {
        console.log("vuelidate error")
      } else {
        if (this.formData.id > 0) {
          this.$axios.post('subject/edit', this.formData).then((res) => {
            this.hideModal()
            this.overlay_show = false;
            this.reloadTable();
          }).catch(error => {
            this.overlay_show = false;
            this.makeToast("danger", "Error", "save_error")
            console.log(error)
          });
        }
        else
        {
          this.$axios.post('subject/create', this.formData).then((res) => {
            //this.t_items = res.data
            this.formData.lessonId = 0
            this.formData.description = ""
            this.formData.id = 0
            this.hideModal()
            this.search();
          }).catch(error => {
            this.overlay_show = false;
            this.makeToast("danger", "Error", error)
            console.log(error)
          });
        }

      }


    }

  },
}
